"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/contexts/AuthContext";
import { toast } from "sonner";
import { Loader2 } from "lucide-react";
import Image from "next/image";

export default function AuthPage() {
  const { signIn, signUp, user, isLoading: authLoading } = useAuth();
  const router = useRouter();

  const [isSignup, setIsSignup] = useState(true); // Default to signup like Monday.com
  const [isLoading, setIsLoading] = useState(false);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  if (user && !authLoading) {
    router.replace("/");
    return null;
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      if (isSignup) {
        if (password !== confirmPassword) {
          toast.error("Passwords do not match");
          return;
        }
        const { error } = await signUp(email, password, name);
        if (error) return toast.error(error.message);
        toast.success("Account created!");
      } else {
        const { error } = await signIn(email, password);
        if (error) return toast.error("Invalid credentials");
        toast.success("Welcome back!");
      }
      router.push("/");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen w-full flex font-sans">

      {/* Left Side - Form */}
      <div className="w-full lg:w-[45%] xl:w-[40%] bg-white flex flex-col justify-center items-center p-8 lg:p-12 animate-in fade-in duration-500">
        <div className="w-full max-w-[400px] flex flex-col items-center text-center">

          <h1 className="text-3xl lg:text-[2.5rem] font-bold text-[#333333] mb-3 tracking-tight">
            Welcome to ServiceDesk
          </h1>

          <p className="text-[#676879] mb-8 text-base">
            {isSignup ? "Get started - it's free. No credit card needed." : "Log in to access your workspace."}
          </p>

          {/* Google Button Mockup */}
          <button
            type="button"
            className="w-full flex items-center justify-center gap-3 bg-white border border-[#d0d4e4] rounded-md py-2.5 px-4 text-[#333333] font-medium hover:bg-slate-50 transition-colors mb-6 group"
            onClick={() => toast.info("Google Sign In is not implemented yet")}
          >
            <svg viewBox="0 0 24 24" width="18" height="18" xmlns="http://www.w3.org/2000/svg" className="group-hover:scale-105 transition-transform">
              <g transform="matrix(1, 0, 0, 1, 27.009001, -39.238998)">
                <path fill="#4285F4" d="M -3.264 51.509 C -3.264 50.719 -3.334 49.969 -3.454 49.239 L -14.754 49.239 L -14.754 53.749 L -8.284 53.749 C -8.574 55.229 -9.424 56.479 -10.684 57.329 L -10.684 60.329 L -6.824 60.329 C -4.564 58.239 -3.264 55.159 -3.264 51.509 Z" />
                <path fill="#34A853" d="M -14.754 63.239 C -11.514 63.239 -8.804 62.159 -6.824 60.329 L -10.684 57.329 C -11.764 58.049 -13.134 58.489 -14.754 58.489 C -17.884 58.489 -20.534 56.379 -21.484 53.529 L -25.464 53.529 L -25.464 56.619 C -23.494 60.539 -19.444 63.239 -14.754 63.239 Z" />
                <path fill="#FBBC05" d="M -21.484 53.529 C -21.734 52.809 -21.864 52.039 -21.864 51.239 C -21.864 50.439 -21.734 49.669 -21.484 48.949 L -21.484 45.859 L -25.464 45.859 C -26.284 47.479 -26.754 49.299 -26.754 51.239 C -26.754 53.179 -26.284 54.999 -25.464 56.619 L -21.484 53.529 Z" />
                <path fill="#EA4335" d="M -14.754 43.989 C -12.984 43.989 -11.404 44.599 -10.154 45.789 L -6.734 42.369 C -8.804 40.429 -11.514 39.239 -14.754 39.239 C -19.444 39.239 -23.494 41.939 -25.464 45.859 L -21.484 48.949 C -20.534 46.099 -17.884 43.989 -14.754 43.989 Z" />
              </g>
            </svg>
            <span>Continue with Google</span>
          </button>

          <div className="flex w-full items-center gap-4 mb-6">
            <div className="h-px bg-[#d0d4e4] flex-1"></div>
            <span className="text-[#676879] text-sm">Or</span>
            <div className="h-px bg-[#d0d4e4] flex-1"></div>
          </div>

          <form onSubmit={handleSubmit} className="w-full flex flex-col gap-4">
            {isSignup && (
              <input
                type="text"
                placeholder="Full Name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
                className="w-full px-4 py-2.5 border border-[#d0d4e4] rounded-md text-[#333333] placeholder:text-[#676879] focus:outline-none focus:border-[#0073ea] focus:ring-1 focus:ring-[#0073ea] transition-all"
              />
            )}

            <input
              type="email"
              placeholder="name@company.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              className="w-full px-4 py-2.5 border border-[#d0d4e4] rounded-md text-[#333333] placeholder:text-[#676879] focus:outline-none focus:border-[#0073ea] focus:ring-1 focus:ring-[#0073ea] transition-all"
            />

            <input
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              className="w-full px-4 py-2.5 border border-[#d0d4e4] rounded-md text-[#333333] placeholder:text-[#676879] focus:outline-none focus:border-[#0073ea] focus:ring-1 focus:ring-[#0073ea] transition-all"
            />

            {isSignup && (
              <input
                type="password"
                placeholder="Confirm password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
                className="w-full px-4 py-2.5 border border-[#d0d4e4] rounded-md text-[#333333] placeholder:text-[#676879] focus:outline-none focus:border-[#0073ea] focus:ring-1 focus:ring-[#0073ea] transition-all"
              />
            )}

            <button
              type="submit"
              disabled={isLoading}
              className="w-full bg-[#0073ea] hover:bg-[#0060b9] text-white font-medium py-2.5 rounded-md transition-all mt-2 disabled:opacity-70 flex items-center justify-center"
            >
              {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : 'Continue'}
            </button>
          </form>

          <div className="mt-8 text-[15px] text-[#333333]">
            {isSignup ? "Already have an account?" : "Don't have an account yet?"}{" "}
            <button
              onClick={() => setIsSignup(!isSignup)}
              className="text-[#0073ea] hover:underline font-medium"
            >
              {isSignup ? "Log in" : "Sign up"}
            </button>
          </div>
        </div>
      </div>

      {/* Right Side - Visual */}
      <div className="hidden lg:flex lg:flex-1 bg-[#6161FF] relative justify-center items-center overflow-hidden">
        {/* Abstract Rings for visual interest */}
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[600px] h-[600px] border border-white/10 rounded-full"></div>
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[800px] h-[800px] border border-white/5 rounded-full"></div>

        <div className="relative z-10 w-full max-w-2xl px-12 animate-in slide-in-from-right duration-700">
          <div className="relative aspect-[4/3] w-full">
            <Image
              src="/assets/auth_hero.png"
              alt="Dashboard Illustration"
              fill
              className="object-contain drop-shadow-2xl"
              priority
            />
          </div>
        </div>
      </div>

    </div>
  );
}
